.detail_box {
    width: 95%;
    margin: 150px auto 10%;
}

.detail_box .movie_container {
    width: 100%;
    margin: 0 auto 2rem;
}

.detail_box .movie-poster img {
    width: 100%;
}

.detail_box .detail_title {
    width: 100%;
    margin-bottom: 1.3rem;
}

.detail_box .detail_title h2 {
    color: #d4d4d4;
    font-size: 1.6rem;
    font-weight: 700;
}

.detail_box .detail_divider {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.detail_box .detail_divider .movie_container {
    width: 48%;
    cursor: pointer;
    animation: fadeInUp .5s ease-in-out;
}

@keyframes fadeInUp {
    from {
        opacity: 0;
        transform: translateY(20px);
    }

    to {
        opacity: 1;
        transform: translateY(0);
    }
}

.detail_box .detail_divider .movie_container:hover .movie-info h4 {
    color: #AEEA00;
}

.detail_box .detail_divider .movie_container:hover .movie-info div.star {
    color: #AEEA00;
}

.detail_box .detail_divider .movie_container .movie-poster img {
    width: 100%;
}

.detail_box .detail_divider .movie_container .movie-info {
    margin-top: 0.5rem;
    text-align: center;
}

.detail_box .detail_divider .movie_container .movie-info h4 {
    font-size: 0.9rem;
    font-weight: 400;
    color: #d4d4d4;
    margin-bottom: 0.3rem;
    transition: all .3s;
}

.detail_box .detail_divider .movie_container .movie-info span {
    display: flex;
    justify-content: center;
    gap: 0.3rem;
    font-size: 0.7rem;
    color: #888;
    font-weight: 400;
}

.detail_box .detail_divider .movie_container .movie-info div.statr {
    transition: all .3s;
}

.movie-detail {
    width: 95%;
    margin: 150px auto 15%;
}

.movie-detail .detail_box .img_box img {
    width: 100%;
    animation: fadeInUp .5s ease-in-out;
}

.movie-detail .detail_box .content_box {
    margin-top: 1.4rem;
}

.movie-detail .detail_box .content_box .detail_title {
    display: flex;
    justify-content: center;
}

.movie-detail .detail_box .content_box .detail_title .movie-title {
    color: #d4d4d4;
    font-size: 1.4rem;
    font-weight: 800;
    letter-spacing: 2px;
}

.movie-detail .detail_box .content_box .title_box {
    margin-bottom: 1rem;
    display: flex;
    justify-content: center;
}

.movie-detail .detail_box .content_box .title_box .average {
    font-size: 0.9rem;
    color: #888;
    font-weight: 400;
}

.movie-detail .detail_box .content_box .title_box .average .star {
    color: #AEEA00;
    margin-right: 0.2rem;
}

.movie-detail .detail_box .content_box .overview {
    color: #d4d4d4;
    font-size: 0.9rem;
    font-weight: 300;
    line-height: 1.8;
    text-align: center;
}

@media screen and (min-width: 1025px) {
    .detail_box .detail_divider .movie_container {
        width: 23%;
        height: auto;
        animation: fadeInUp .5s ease-in-out;
    }

    @keyframes fadeInUp {
        from {
            opacity: 0;
            transform: translateY(20px);
        }

        to {
            opacity: 1;
            transform: translateY(0);
        }
    }

    .detail_box .detail_divider .movie_container .movie-poster img {
        height: 350px;
        object-fit: cover;
    }
}