
@import './reset.css';

* {
  box-sizing: border-box;
}

body {
  font-family: "Pretendard", sans-serif;
  background: #000;
}

body::-webkit-scrollbar {
  width: 8px;
}

body::-webkit-scrollbar-thumb {
  background: #686868;
  border-radius: 2px;
  transition: all .3s;
}

body::-webkit-scrollbar-thumb:hover {
  background: #7da701;
}

.header-wrap {
  width: 100%;
  height: 100px;
  background-color: #AEEA00;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
}

.header-wrap .header-left-wrap {
  width: 95%;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}

.header-wrap .header-left-wrap .header-logo {
  color: #000;
  font-size: 4rem;
  font-weight: 900;
  letter-spacing: 2px;
  cursor: pointer;
}

.header-wrap ul {
  display: flex;
  justify-content: right;
  align-items: center;
  gap: 2rem;
  list-style: none;
  background: #AEEA00;
  padding-bottom: 0.1rem;
  width: 100%;
  height: 30px;
}

.header-wrap ul li a {
  color: #000;
  font-size: 0.8rem;
  font-weight: 300;
  text-decoration: none;
  transition: all .3s;
}

.header-wrap ul li a:hover {
  color: #fff;
}

.footer {
  width: 100%;
  height: auto;
  padding: 2rem 0;
  text-align: center;
  color: #888;
  font-size: 0.7rem;
  line-height: 1.7;
  background: #131313;
}

.footer .header-logo {
  color: #AEEA00;
  font-size: 0.9rem;
  font-weight: 300;
  letter-spacing: 2px;
  cursor: pointer;
}

@media screen and (min-width: 1025px) {
  .header-wrap ul {
    width: 100%;
    margin-right: 5%;
  }
  
  .header-wrap ul li {
    position: relative;
    padding: 0.1rem;
  }

  .header-wrap ul li::after {
    position: absolute;
    content: '';
    bottom: 0;
    left: 0;
    width: 0;
    height: 100%;
    background: #fff;
    transition: all .3s;
  }

  .header-wrap ul li a {
    font-size: 0.9rem;
    z-index: 99;
    position: relative;
  }

  .header-wrap ul li:hover::after {
    width: 100%;
  }

  .header-wrap ul li:hover a {
    color: #AEEA00 !important;
    z-index: 99;
  }

  .header-wrap ul li a:last-child {
    margin-right: 0.7rem;
  }

  .page-container .movie_box {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  .page-container .movie_box .movie_section {
    width: 10%;
    height: auto;
    animation: fadeInUp .5s ease-in-out;
  }
}