.page-container {
  margin-top: 150px;
}

.page-container .movie_box {
  width: 95%;
  margin: 0 auto 15%;
}

.page-container .movie_box .title-box {
  width: 95%;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
}

.page-container .movie_box .title-box h2 {
  color: #fff;
  font-size: 1.4rem;
  font-weight: 700;
  cursor: default;
}

.page-container .movie_box .title-box a {
  color: #888;
  font-size: 0.8rem;
  font-weight: 300;
  text-decoration: none;
  transition: all .3s;
}

.page-container .movie_box .title-box a:hover {
  color: #AEEA00;
}

/* .page-container .movie_box .movie_section {
  width: 100%;
  display: flex;
  flex-wrap: nowrap;
  overflow-x: scroll;
} */

.page-container .movie_box .movie_section {
  width: 100%;
  margin-bottom: 15%;
}

.page-container .movie_box .movie_section .movie_container {
  margin-bottom: 10%;
  background: rgba(174, 234, 0, 0.8);
  backdrop-filter: blur(5px);
  transition: all .3s;
  cursor: pointer;
}

.page-container .movie_box .movie_section .movie_container:hover {
  background: #AEEA00;
}

.page-container .movie_box .movie_section .movie_container .movie-poster img {
  width: 100%;
  object-fit: cover;
}

.page-container .movie_box .movie_section .movie_container .movie-info {
  margin: 1rem 0;
  color: #000;
}

.page-container .movie_box .movie_section .movie_container .movie-info h4 {
  font-size: 1.1rem;
  font-weight: 800;
  display: flex;
  justify-content: right;
  margin-right: 1rem;
}

.page-container .movie_box .movie_section .movie_container .movie-info span {
  display: flex;
  justify-content: right;
  margin-right: 1rem;
  margin: 1rem;
  padding-bottom: 1rem;
  font-size: 0.8rem;
}

@media screen and (min-width: 1025px) {
  .page-container {
    margin-top: 200px;
  }

  .page-container .movie_box .movie_section {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  .page-container .movie_box .movie_section .movie_container {
    width: 20%;
    margin-bottom: 0;
    animation: fadeInUp .5s ease-in-out;
  }
}